* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}
h2,h3,h4,h5{
  font-weight: 500;
}

.container {
  position: relative;
  width: 100%;
  height: 100%;
  max-width: 1000vh;
  min-height: 100vh;
  background: #fff;
  display: grid;
  grid-template-columns: 1fr 2fr;
  box-shadow: 0 35px 55px rgba(0,0,0,0.1);

}
.container .left-side{
  position: relative;
  background: #003147;
  padding: 20px;
  
}
.container .right-side{
  position: relative;
  background: #fff;
  padding: 20px;
}
.name{
  color: #fff;
  position:relative;
  font-size: 1em;
  text-align: center;
  
  width: 100%;
  height: 80px;
  
  
}
.icon{
  position: absolute;
  left: -5px;
  
}
.contact-info{
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 2px; 
}
.contact-text{
  padding-left:"10px"
}
.contact-info-flex{
  flex: content;
}
.left-side .contact-info{
  color: #fff;
  padding: 20px;
}
.contact-info h3,.achievements h3{
  margin: 10px 50px 20px 0;
  color:#03a9f4;
}
.contact-info span{
  padding:10px;
  margin-bottom: 20px;
  font-weight: 500;
  letter-spacing: 2px;
  line-height: 20px;;
  cursor: pointer;
  
}
.education,.achievements,.skillsClass{
  margin: 30px 50px 40px 30px;
  color: #fff;
  letter-spacing: 1px;
  line-height: 20px;
  
  
}
.education li{
  margin-bottom: 15px;
  
}
.education h3{
  margin: 10px 50px 20px 0;
  color:#03a9f4;
  
}
.skillsClass h3{
  margin: 10px 50px 20px 0;
  color:#03a9f4;
  
}

.education-timeline{
  color:#03a9f4; 
}
.right-side{
  padding: 40px;
  letter-spacing: 1px;
  line-height: 20px;
}
.title2{
  color:#2a7da2;
  letter-spacing: 1px;
  margin-bottom: 2px;
  font-weight: bold;
}
p{
  color:#333;
}

.about .box .year_company{
  min-width: 150px;
}
.about .box .year_company h5{
  color: #333;
  font-weight:bold;
}
.about .box .text h4{
  color: #2a7da2;
  font-size: 16px;
}
.text li{
  margin-left: 20px;
}
.bold, .text h4,.year_company h4{
  font-weight: bold;
}
table, th, td {
  border:1px solid black;
}
table{
  width: 100%;
}
table .topic{
  width: 30%;
}
.portfolio li{
  margin-left: 20px;
}
.about,.skills,.portfolio{
  padding: 0px 20px 20px 20px;
}

.box{
padding: 10px 20px 0 0;
}
.nobold{
  font-weight: lighter;
}
@media(max-width: 1000px){
  .container{
    margin: 10px;
    grid-template-columns: repeat(1, 1fr);
  }
}
